}

.navBar a {
  font-weight: 500;
  margin: 20px;
  
  padding: 5px 15px;
  border-radius: 5px;
  color: #457938;
  text-decoration: none;
  font-size: 20px;
  -webkit-transition: 0.2s;
  transition: 0.2s;
  cursor: pointer;
  font-family: "Open Sans", sans-serif;
}

.nav-bar a:hover {
  color: #434646e1;
}

.regBtn {
  border-radius: 25px !important;
  color: white !important;
