.header1:last-of-type {
  width:0px;
  -webkit-animation: reveal 5s;
          animation: reveal 5s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.header1 span:last-of-type  {
  margin-left:-355px;
  -webkit-animation: slidein 5s;
          animation: slidein 5s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes showup {
  0% {opacity:0;}
  20% {opacity:1;}
  80% {opacity:1;}
  100% {opacity:1;}
}

@keyframes showup {
  0% {opacity:0;}
  20% {opacity:1;}
  80% {opacity:1;}
  100% {opacity:1;}
}

@-webkit-keyframes slidein {
  0% { margin-left:-1500px; }
  20% { margin-left:-1500px; }
  35% { margin-left:0px; }
  100% {opacity:1; margin-left:0px; }
}

@keyframes slidein {
  0% { margin-left:-1500px; }
  20% { margin-left:-1500px; }
  35% { margin-left:0px; }
  100% {opacity:1; margin-left:0px; }
}

@-webkit-keyframes reveal {
  0% {opacity:0;width:0px;}
  20% {opacity:1;width:0px;}
  30% {width:355px;}
  80% {opacity:1;}
  100% {opacity:1;width:355px;}
}

@keyframes reveal {
  0% {opacity:0;width:0px;}
  20% {opacity:1;width:0px;}
  30% {width:355px;}
  80% {opacity:1;}
  100% {opacity:1;width:355px;}
}

